import React, { Component } from "react";
import { hot } from "react-hot-loader";

class CreateCompetitionBody extends Component {
  render() {
    const {
      defaultName,
      defaultType,
      defaultTimeLimit,
      defaultBoardSize,
      onChangeName,
      onChangeType,
      onChangeTimeLimit,
      onChangeBoardSize,
    } = this.props;

    return (
      <div>
        <label></label>
        <input
          value={defaultName}
          onChange={(e) => onChangeName(e.target.value)}
        />
        <br />
        <label></label>
        <select
          value={defaultType}
          onChange={(e) => onChangeType(e.target.value)}
        >
          <option value="round-robin"></option>
          <option value="knockout"></option>
        </select>
        <br />
        <label></label>
        <input
          type="range"
          min="0.1"
          max="20"
          step="0.1"
          value={defaultTimeLimit}
          onChange={(e) => onChangeTimeLimit(e.target.value)}
        ></input>
        <button
          onClick={(e) => {
            onChangeTimeLimit(
              parseFloat((parseFloat(defaultTimeLimit) + 0.1).toFixed(1))
            );
          }}
        >
          {">"}
        </button>
        <span>{defaultTimeLimit} min</span>
        <br />
        <label></label>
        <select
          value={defaultBoardSize}
          onChange={(e) => onChangeBoardSize(e.target.value)}
        >
          {[4, 6, 8, 10, 12, 14].map((size, index) => (
            <option key={index} value={size}>
              {size}
            </option>
          ))}
        </select>
      </div>
    );
  }
}

export default hot(module)(CreateCompetitionBody);
